import React from 'react';

function Faq() {

    return (
<>
<section id="faq" className="padd-section text-center wow fadeInUp">
        <div className="container">
        <h1>FAQ</h1>
          <br/>            
          
          <div className="row">
              <div className="col-md-6 col-lg-4">
                  <div className="feature-block">
              <h4>
                What are MetaMaps?
              </h4>
              <p>
              The first ever collection of 10,000 metaverse map NFTs living on the Ethereum Blockchain. 
              </p>

            </div>
          </div>

          <div className="col-md-6 col-lg-4">
              <div className="feature-block">
          <h4>
            How are MetaMaps created?
          </h4>
          <p>
          Our MetaMap generator randomizes the composition of terrains to
          produce time-based 1/1 unique maps, each evolving over time (coming soon)
          </p>
          </div>
      </div>

          <div className="col-md-6 col-lg-4">
              <div className="feature-block">
          <h4>
            What devices are supported by MetaMaps
          </h4>
          <p>
          Both Mobile and Desktop are supported for minting. At initial metaverse release, only Desktop will be supported for gameplay.
          </p>
          </div>
      </div>

          <div className="col-md-6 col-lg-4">
              <div className="feature-block">
          <h4>
            What does ownership entail?
          </h4>
          <p>
          Owners have full rights to use the map terrain data and art, including building other derivative worlds in other games/art
          </p>
          </div>
      </div>

          <div className="col-md-6 col-lg-4">
              <div className="feature-block">
          <h4>
            How are MetaMaps Rarity determined?
          </h4>
          <p>
            Purple Gems are the only rarity factor beyond the unique 1/1 layout of the MetaMap itself. Other MetaMaps attributes are terrain information, such as altitudes of different terrain types.
            </p>
            </div>
      </div>

          <div className="col-md-6 col-lg-4">
              <div className="feature-block">
          <h4>
          Compatability with other NFTs? 
          </h4>
            <p>
            We will be developing a proof of concept metaverse where 2d PFP could be loaded onto playable characters and other artworks could be displayed within your MetaMap. Support for 3D voxels are also down the track.
            </p>
            </div>
      </div>

              
      </div>
      </div>
        
      </section>     
        </>
    )
          };

export default Faq;