import React, { useEffect, useState} from "react";
import { withRouter, useParams } from "react-router-dom";
import axios from 'axios';


import styled from "styled-components";
//import { create } from "ipfs-http-client";

export const StyledButton = styled.button`
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 13px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 11px 36px;
  border-radius: 50px;
  transition: 0.5s;
  margin-bottom: 30px;
  border: 2px solid #71c55d;

  color: #71c55d;
  text-decoration: none;
`;






export default function Detail() {
  
  const id2 = String(useParams()['id']);
  const [data,setData]=useState({
    dna:'',
    mountain:'',
    trees:'',
    grass:'',
    sand:'',
    gems:'',
  }
  );

  
  useEffect(() => {
    document.title = "MetaMaps";
  }, []);
  


  useEffect(()=>{
    axios.get("./maps/" + id2 + ".json")
        .then(res=>{
            //console.log(res)
            let attrData=res.data;
            

            setData({dna:attrData.dna, 
              mountain:attrData.attributes[0].value,
              trees:attrData.attributes[1].value,
              grass:attrData.attributes[2].value,
              sand:attrData.attributes[3].value,
              gems:attrData.attributes[4].value})
           
        })
        .catch(err=>{
            console.log(err);
        })
  },[])





  if (id2 !== "" && Number(id2) < 1515 ){
    const imgurl = "./maps/" + id2 + ".png";
    return(
    <>
      <section id="team" className="padd-section text-center wow fadeInUp">
        <div className="container">
          <div className="section-title text-center">
            <h2>MetaMap #{id2}</h2>
          </div>
        </div>
        <div className="container">
          <div className="row">
            <div className="col-md-6 col-md-4 col-lg-4">
              <div className="team-block bottom">
                <img src={imgurl} className="img-responsive" alt="img" />
                <div className="team-content">
                  <h6>Gems: {data.gems}</h6>
                </div>
              </div>
            </div>
            <div className="col-md-6 col-md-4 col-lg-4">
              <div className="team-block bottom" text-align="left" align="center">
                
                <h3>
                Terrain Data
                </h3>
                <table>
                  <thead>
                    <tr>
                      <th>Terrain Type</th>
                      <th>Altitude</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>Mountain</td>
                      <td>{data.mountain}</td>
                    </tr>
                    <tr>
                      <td>Trees</td>
                      <td>{data.trees}</td>
                    </tr>
                    <tr>
                      <td>Grass</td>
                      <td>{data.grass}</td>
                    </tr>
                    <tr>
                      <td>Sand</td>
                      <td>{data.sand}</td>
                    </tr>
                    <tr>
                      <td>Water</td>
                      <td>0.00</td>
                    </tr>
                  </tbody>
                  </table>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
    )
  }

  
  
  
}