import React from 'react';
import cloud from "./../img/svg/1.gif";
import planet from "./../img/svg/2.png";
import astroid from "./../img/svg/3.png"
import banner from "./../img/newDemo.png"
function Intro() {

    return(
    <>
    <section id="get-started" className="padd-section text-center wow fadeInUp">
        <div className="container">
          <div className="section-title text-center">
            <h2>What are MetaMaps?</h2>
            <img src={banner} alt="Banner Imgs" />
            <br/>  <br/>
            <p>MetaMaps is the first ever NFT collection of 10,000 unique and algorithmically generated map terrains that exist on the Ethereum blockchain. Each map will be a place where Explorers could gather for their new adventures. Building and exploring cross-collection adventure maps.
            </p>
           
            <p> Our MetaMap generator randomizes the composition of terrains to produce time-based 1/1 unique maps, each ready to be built into a new adventure and each evolving over time. We are concurrently building out two metaverse games for you to play 1) 2D MMORPG 2) NFTWorlds (minecraft world).
              Some maps are rarer than others (with purple gems), but they are all unique. There are a few maps that are very special, the ultra-rares. If you are fortunate enough to mint one of these maps, you better hold onto the riches. </p>
            <p />
          </div>
        </div>
        <div className="container">
          <div className="row">
            <div className="col-md-6 col-lg-4">
              <div className="feature-block">
                
                <img src={cloud} alt="img" className="img-fluid" />
                <h4>Multiverse</h4>
                <p>MetaMaps is constantly evolving and building many versions of the world you own.</p>
              </div>
            </div>
            <div className="col-md-6 col-lg-4">
              <div className="feature-block">
                <img src={planet} alt="img" className="img-fluid" />
                <h4>Interoperability</h4>
                <p>Characters/items across our multiverse aim to be cross compatible between games</p>
              </div>
            </div>
            <div className="col-md-6 col-lg-4">
              <div className="feature-block">
                <img src={astroid} alt="img" className="img-fluid" />
                <h4>Full ownership</h4>
                <p>Owners have full rights to use the map terrain data, including building other derivative worlds</p>
              </div>
            </div>
          </div>
        </div>
      </section>      
    </>
    )}
    
export default Intro;