import React from 'react';
import team1 from './../img/team/1.png';
import team2 from './../img/team/2.png';
import team3 from './../img/team/3.png';
import team4 from './../img/team/4.png';
function Team() {

    return(
    <>
      <section id="team" className="padd-section text-center wow fadeInUp">
        <div className="container">
          <div className="section-title text-center">
            <h2>Team Members</h2>
          </div>
        </div>
        <div className="container">
          <div className="row">
            <div className="col-md-6 col-md-4 col-lg-3">
              <div className="team-block bottom">
                <img src={team3} className="img-responsive" alt="img" />
                <div className="team-content">
                  <span>Founder</span>
                  <h4>pencil.eth</h4>
                </div>
              </div>
            </div>
            <div className="col-md-6 col-md-4 col-lg-3">
              <div className="team-block bottom">
                <img src={team2} className="img-responsive" alt="img" />
                <div className="team-content">
                  <span>Designer</span>
                  <h4>helio.eth</h4>
                </div>
              </div>
            </div>
            <div className="col-md-6 col-md-4 col-lg-3">
              <div className="team-block bottom">
                <img src={team1} className="img-responsive" alt="img" />
                <div className="team-content">
                  <span>Developer</span>
                  <h4>maths.eth</h4>
                </div>
              </div>
            </div>
            <div className="col-md-6 col-md-4 col-lg-3">
              <div className="team-block bottom">
                <img src={team4} className="img-responsive" alt="img" />
                <div className="team-content">
                  <span>Community Manager</span>
                  <h4>folks.eth</h4>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
)}
    
export default Team;