import React from 'react';
import asteroid from "./../img/svg/asteroid.svg";
import paint from "./../img/svg/paint-palette.svg";
import tool from "./../img/svg/design-tool.svg";
import vector from "./../img/svg/vector.svg";

function Roadmap() {

    return (
    <>

      <section id="Roadmap" className="padd-section text-center wow fadeInUp">
        <div className="container">
          <div className="section-title text-center">
            <h2>Map of Roads</h2>
          </div>
        </div>
        <div className="container">
          <div className="row">
            <div className="col-md-6 col-lg-3">
              <div className="feature-block">
                <img src={asteroid} alt="img" className="img-fluid" />
                <h4>25% Minted</h4>
                <p>Airdrop maps to our most loyal early explorers (Lottery or MATIC)</p>
              </div>
            </div>
            <div className="col-md-6 col-lg-3">
              <div className="feature-block">
                <img src={paint} alt="img" className="img-fluid" />
                <h4>50% Minted</h4>
                <p>Build out NFT compatible 3D metaverses, decided upon by our community</p>
              </div>
            </div>
            <div className="col-md-6 col-lg-3">
              <div className="feature-block">
                <img src={tool} alt="img" className="img-fluid" />
                <h4>75% Minted</h4>
                <p>Open-source terrain data for other projects to build upon</p>
              </div>
            </div>
            <div className="col-md-6 col-lg-3">
              <div className="feature-block">
                <img src={vector} alt="img" className="img-fluid" />
                <h4>100% Minted</h4>
                <p>Start development of map evolution mechanism (e.g. weather, events)</p>
              </div>
            </div>
          </div>
          <div className="row">
            <div className="col-md-6 col-lg-3">
              <div className="feature-block">
                <h4>+ more</h4>
                <p>Regular updates + live demo to try out</p>
              </div>
            </div>
            <div className="col-md-6 col-lg-3">
              <div className="feature-block">
                <h4>+ more</h4>
                <p>2d MetaMaps games</p>
              </div>
            </div>
            <div className="col-md-6 col-lg-3">
              <div className="feature-block">
                <h4>+ more</h4>
                <p>MetaMaps treasure hunt with rewards and NFT art exhibitions</p>
              </div>
            </div>
            <div className="col-md-6 col-lg-3">
              <div className="feature-block">
                <h4>+ more</h4>
                <p>Floor sweeps using secondary sales royalties</p>
              </div>
            </div>
          </div>
        </div>
      </section>

    </>
)}
    
export default Roadmap;