import React, { useEffect, useState} from "react";

import { useDispatch, useSelector } from "react-redux";
import { connect } from "../redux/blockchain/blockchainActions";
import { fetchData } from "../redux/data/dataActions";

import * as s from "../styles/globalStyles";
import styled from "styled-components";
//import { create } from "ipfs-http-client";

export const StyledButton = styled.button`
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 13px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 11px 36px;
  border-radius: 50px;
  transition: 0.5s;
  margin-bottom: 30px;
  border: 2px solid #71c55d;

  color: #71c55d;
  text-decoration: none;
`;

function Minter() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  //const data = useSelector((state) => state.data);
  const [feedback, setFeedback] = useState("");
  const [claimingNft, setClaimingNft] = useState(false);

  const claimNFTs = (_amount) => {
    if (_amount <= 0) {
      return;
    }
    

    setFeedback("Cartographer minting MetaMap...");
    setClaimingNft(true);
    blockchain.smartContract.methods
      .mint(blockchain.account, _amount)
      .send({
        gasLimit: (285000 * _amount).toString(),
        to: "0x8236afC3447C55a499dc62D62080321563d47342",
        from: blockchain.account,
        value: blockchain.web3.utils.toWei((0.03 * _amount).toString(), "ether"),
      })
      .once("error", (err) => {
        console.log(err);
        setFeedback("Sorry, something went wrong please try again later.");
        setClaimingNft(false);
      })
      .then((receipt) => {
        setFeedback(
          "Your MetaMap has been repaired by the Cartographer. Ready to explore at OpenSea.io"
        );
        setClaimingNft(false);
        dispatch(fetchData(blockchain.account));
      });
  };

  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  useEffect(() => {
    getData();
  }, [blockchain.account]);
  
  useEffect(() => {
    document.title = "MetaMaps";
  }, []);

  return (
    <div>  
                {blockchain.account === "" ||
                blockchain.smartContract === null ? (
                  <s.Container ai={"center"} jc={"center"}>

              
                    <StyledButton className="btn-get-started scrollto"
                      onClick={(e) => {
                        e.preventDefault();
                        dispatch(connect());
                        getData();
                      }}
                    >
                      CONNECT
                    </StyledButton>
                    {blockchain.errorMsg !== "" ? (
                      <>
                        <s.SpacerSmall />
                        <p>{blockchain.errorMsg}</p>
                      </>
                    ) : null}
                  </s.Container>
                ) : (
                  
                  <s.Container ai={"center"} jc={"center"} fd={"row"}>

                        <s.SpacerSmall />
                    <StyledButton className="btn-get-started scrollto"
                      disabled={claimingNft ? 1 : 0}
                      onClick={(e) => {
                        e.preventDefault();
                        claimNFTs("1");
                        getData();
                      }}
                     >
                      {claimingNft ? "BUSY" : "Mint 1 for 0.03 ETH"}
                    </StyledButton>
                    <StyledButton className="btn-get-started scrollto"
                      disabled={claimingNft ? 1 : 0}
                      onClick={(e) => {
                        e.preventDefault();
                        claimNFTs("5");
                        getData();
                      }}
                    >
                      {claimingNft ? "BUSY" : "Mint 5 for 0.15 ETH"}
                    </StyledButton>
                    <s.SpacerSmall />
                      <p>{feedback}</p>
                  </s.Container>
                  
                )}
          
        </div>
  )
}

export default Minter;
